@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
*{padding:0;margin:0}*,*::before,*::after{box-sizing:inherit}html{box-sizing:border-box;width:100%;max-width:100%}body{min-width:320px;position:relative;font-weight:400;color:#fff;margin:0;background-color:#1c0419ed}a{text-decoration:none;color:inherit;display:inline-block;outline:none}ul,li{list-style:none;outline:none;margin:0;padding:0}h1,h2,h3,h4,h5,h6{margin:0;font-size:inherit}p{margin:0}button,input,textarea{outline:none}button{border:none}.App{position:relative;height:inherit;background-size:cover;background-image:url(/moviestorm/static/media/stranger_things.ed0e7f4e.jpg);background-repeat:no-repeat;background-position:top center}.wrapper{position:absolute;height:100vh;width:100%;top:0;left:0;background-color:rgba(0,0,0,0.6)}.container{width:80%;margin:0 auto}.pagination{margin:15px auto;display:flex;list-style:none;outline:none}.pagination .break-me a{border:none}.pagination>.active>a{background-color:#fff;border-color:#fff;color:#1c0419ed}.pagination>li>a{border:1px solid #e4d1e2ed;padding:5px 10px;outline:none;cursor:pointer;border-radius:20%;margin-right:5px}.pagination>.active>a,.pagination>.active>span,.pagination>.active>a:hover,.pagination>.active>span:hover,.pagination>.active>a:focus,.pagination>.active>span:focus{background-color:#ddbbd9;border-color:#ddbbd9;outline:none}.pagination>li>a,.pagination>li>span{color:#fff}.pagination>li:first-child>a,.pagination>li:first-child>span,.pagination>li:last-child>a,.pagination>li:last-child>span{border-radius:unset;border:none}

.footer{background-color:#10050f;overflow:auto;font-size:0.9rem;padding:20px 50px;margin-top:20px}.footer p{margin-bottom:1.5rem}.footer .footer-cols{display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:2rem}.footer li{line-height:1.9}@media (max-width: 600px){.footer{padding:20px}.footer .footer-cols{grid-gap:1rem;grid-template-columns:repeat(2, 1fr)}}@media (max-width: 475px){p{text-align:center}.footer .footer-cols{grid-gap:1rem;grid-template-columns:repeat(1, 1fr);text-align:center}}

