.home .MuiTab-textColorPrimary.Mui-selected{color:#2e1f35}.home .makeStyles-root-13 .PrivateTabIndicator-colorPrimary-15{background-color:#2e1f35 !important}.home .makeStyles-root-13 .makeStyles-root-12 button{background-color:#4c3f89 !important}.btn{padding:10px 20px}.btn__inner{display:flex;align-items:center;font-size:20px;color:#fff}.tab-1{display:flex;flex-direction:column;align-items:center;text-align:center}.tab-1 p{font-size:18px;padding:10px 0}.tab-1__img{width:60%;margin:10px auto}.tab-2__top{display:flex;justify-content:center;align-items:center;flex-wrap:wrap}.tab-2__top p{font-size:18px}.tab-2__bottom{display:grid;grid-template-columns:1fr 1fr 1fr;align-items:center;margin-top:30px;grid-gap:10px}.tab-2__img{width:100%}.tab-3__top{display:flex;justify-content:center;align-items:center;flex-wrap:wrap;text-align:center}.tab-3__top p{font-size:18px}.tab-3 .table{width:100%;margin-top:2rem;border-collapse:collapse;border-spacing:0}.tab-3 .table thead th{text-transform:uppercase;padding:0.8rem}.tab-3 .table td,.tab-3 .table th{padding:10px}.tab-3 .table .table tbody tr td{color:#999;padding:0.8rem 1.2rem;text-align:center}.tab-3 .table tbody tr td:first-child{text-align:left}.tab-3 .table tbody tr:nth-child(odd){background:#252029}@media (max-width: 768px){.tab-2{text-align:center}.tab-2__bottom{grid-template-columns:1fr}}@media (max-width: 650px){.tab-3 .table td,.tab-3 .table th,.tab-3 .table thead th{padding:4px}.table{font-size:12px}}

